.AppTemplate-container {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
}
@media screen and (max-width: 1280px) {
  .AppTemplate-main-wrapper {
    width: 100%;
  }
}
@media screen and (max-width: 768px){
  .AppTemplate-toolbar {
    display: none;
  }
}
