.skeleton-image-container {
    position: relative;
    padding-top: 100%;
}

.skeleton-image-element {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.skeleton-image-child-container {
    margin-top: 1em;
    position: relative;
    padding-top: 20%;
}

.skeleton-image-child-element {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}