.cart-dialog-container {
    padding: 1em;
    padding-bottom: 0;
}
.cart-dialog-image {
    width: 5em;
    display: block;
    margin: auto;
}
.cart-dialog-button-container {
    display: flex;
    width: 100%;
    min-width: 350px;
    justify-content: space-between;
    margin-top: 2em;
}
.cart-dialog-button {
    padding: 1em 0.5em 1em 0.5em;
    width: 45%;
    text-align: center;
    border: none;
}
.cart-dialog-text {
    text-align: center;
    font-weight: bold;
}
.btn-1 {
    background-color: white;
    border: 1px solid var(--main-bg-color);
    color: var(--main-bg-color);
}
.btn-2 {
    background-color: var(--main-bg-color);
    color: white;
}

@media screen and (max-width: 768px){
    .cart-dialog-button-container{
        min-width: 300px;
        font-size: 0.9em;
    }

}