.toolbar-mobile-container {
    width: 100%;
    z-index: 10;
    padding: 0 !important;
    margin-top: 0;
    margin-bottom: 0;
    -webkit-padding-start: 0;
    list-style-type: none;
}

.toolbar-mobile-container li:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.toolbar-mobile-element {
    padding: 1em 1em 1em 1em;
    font-weight: bold;
    font-size: 0.9em;
    color: black;
    /*overflow-x: hidden;*/
    /*word-break: keep-all;*/
    width: 270px;
    box-sizing: border-box;
}

.toolbar-mobile-element:hover {
    cursor: pointer;
}

.toolbar-mobile-icon {
    transition: transform 0.2s ease-out;
    width: 0.6em;
    height: 1em;
    margin-right: 1em;
    color: black;
    font-size: 13px;
    align-self: center;
}

.toolbar-mobile-product-element {
    padding: 0.5em 0.5em 0.5em 0.5em;
    overflow: hidden;
    font-size: 0.9em;
}

.toolbar-mobile-product-container {
    background-color: white;
    width: 270px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.4s linear;
}

.toolbar-mobile-product-container li ul {
    border: none !important;
    /*border-bottom: 1px solid rgba(0,0,0,0.1) !important;*/
}

.toolbar-mobile-product-container ul li {
    border: none !important;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1) !important;
    /*border-bottom: 1px solid rgba(0,0,0,0.1) !important;*/
}

.toolbar-mobile-product-container ul li .navigation-left-element-title {
    padding-left: 2em !important;
    /*border-bottom: 1px solid rgba(0,0,0,0.1) !important;*/
}

.toolbar-mobile-product-container ul li ul li {
    border: none !important;
    border-top: 1px solid rgba(0, 0, 0, 0.1) !important;
    padding-left: 3em !important;
    /*border-bottom: 1px solid rgba(0,0,0,0.1) !important;*/
}

.product-open {
    max-height: 100vh;
}


.toolbar-mobile-product-element:hover {
    cursor: pointer;
}

.toolbar-mobile-product-list {
    border-bottom: none !important;
}
