.navigation-left-element {
    font-size: 0.9em;
}
.navigation-left-p-title {
    color: black;
}
.navigation-left-element-child {
    padding: 1em 1em 1em 2em;
    border-top: 1px solid rgba(0,0,0,0.1);
    border-bottom: none !important;
}
.navigation-left-element-title {
    display: flex;
    padding: 1em;
}
.navigation-left-p-title:hover{
    font-weight: bold;
}
.navigation-left-icon {
    width: 2em;
    text-align: center;
    transition: transform 0.1s ease-out;
}
.navigation-left-icon:hover {
    color: var(--main-bg-color);
}
.child-open {
    transform: rotate(180deg);
}

.navigation-left-element-child-ul {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s linear;
        padding: 0;
    -webkit-padding-start: 0;
    list-style-type: none;
}
.element-child-open {
    max-height: 20vh;
}