.sale-item-container {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
}
.sale-item-image-container {
    width: 30%;
    min-width: 5rem;
}
.sale-item-image {
    width: 100%;
    position: relative;
    background-size: cover;
    padding-top: 100%;
    border-radius: 2px;
}

.sale-item-innerText {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
    display: none;
}
.sale-item-name {
    font-size: 0.9rem;
    line-height: 1.2rem;
    height: 2.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
}

.sale-item-detail {
    padding: 0.5em;
}

.sale-item-price-container {
    display: flex;
    flex-direction: row;
    height: 38px;
    margin-top: 0.4rem;
}

.sale-item-price {
    font-size: 12px;
    text-decoration: line-through;
    height: 16px;
}

.sale-item-sale_price {
    font-weight: bold;
    color: crimson;
    font-size: 14px;
}

.sale-item-sale-percent {
    font-size: 13px;
    font-weight: bold;
    position: absolute;
    color: white;
    background-color: #2196f3;
    padding: 5px;
    border-radius: 3px;
    align-self: flex-end;
    bottom: 0;
    right: 0;
}

@media screen and (max-width: 500px) {
    .sale-item-name {
        font-size: 0.8rem;
        line-height: 1rem;
            height: 2rem;
    }

    .sale-item-container:hover {
        transform: none;
    }
}