.header-container {
    width: 100%;
    -webkit-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
    background-color: white;
}

.header-inner-level-0 {
    width: 100%;
    position: relative;
}

.header-wrapper {
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease-in-out;
    position: relative;
}

.header-toolbar-container {
    width: 100%;
    height: 100%;
    /*background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAPUlEQVQoU2NkIAIsZ2BoYCSkDqQokpBCmCKQYThNRFaEUyG6IqwKsSnCUIhLEYpCfIrgCgkpAiskRhFIIQC2ChMyaFD1HAAAAABJRU5ErkJggg==) repeat;*/
    background-color: var(--main-bg-color);

}

.header-toolbar-wrapper {
    width: 80%;
    margin: auto;
    max-width: 1280px;
}
.header-logo {
    height: 4em;
}

@media screen and (max-width: 1280px) {
    .header-toolbar-wrapper {
        width: 100%;
    }
}
@media screen and (max-width: 768px){
    .header-logo {
        height: 3em;
    }
}