.detail-image-container {
    width: 100%;
    margin-top: 5px;;
    /*height: 5em;*/
    /*background-color: red;*/
    /*margin-top: 1em;*/
}

.detail-image-active {
    border : 2px solid var(--main-bg-color);
}
.detail-image-element {
    width: 100%;
    position: relative;
    background-size: cover;
    padding-top: 100%;
    border-radius: 2px;
    box-sizing: border-box;
}
.detail-image-element:hover {
    cursor: pointer;
}