.header-cart-container {
    margin-right: 0;
    padding: 0.7em;
    cursor: pointer;
    display: flex;
    /*border: 1px solid var(--second-bg-color);*/
    position: relative;
    z-index: 7;
    border-radius: 6px;
}

.header-cart-container:hover {
    background-color: rgba(3, 165, 252, 0.1);
}

.header-cart-icon-container {
    position: relative;
}

.header-cart-icon {
    width: 2em;
    height: 2em;
}

.header-cart-icon-number {
    position: absolute;
    height: 1.2em;
    width: 1.2em;
    border-radius: 50%;
    background-color: #ef5350;
    display: flex;
    align-items: center;
    justify-content: center;
    top: -0.5em;
    right: -0.5em;
}

.header-cart-number {
    color: white;
    font-size: 0.8em;
}

.header-cart-list-cart {
    position: absolute;
    background-color: white;
    width: 400px;
    height: auto;
    bottom: 0;
    transform: translate(-70%, 100%);
    z-index: 2;
    /*margin-bottom: -1em;*/
    opacity: 0;
    margin-left: 2em;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.3);
    display: none;
}

.header-cart-list-cart:before {
    content: '';
    /*background-color: blue;*/
    position: absolute;
    right: 0;
    transform: translateY(-100%);
    margin-right: 0.7em;
    border-top: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid white;
    border-left: 10px solid transparent;
    width: 0;
    height: 0;
    transition: opacity 0.4s ease-out;
    display: none;
}

.header-cart-text {
    margin-left: 1em;
}

.header-cart-container:hover .header-cart-list-cart {
    animation: opacity1 0.3s ease-out;
    animation-fill-mode: forwards;
    display: block;
}

.header-cart-container:hover .header-cart-list-cart:before {
    animation: opacity1 0.3s ease-out;
    animation-fill-mode: forwards;
    display: block;
}

.header-cart-empty {
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../../../assets/empty-cart.png");
    width: 50%;
    position: relative;
    padding-top: 70%;
    left: 50%;
    transform: translate(-50%, 10%);
}

.header-cart-inner {
    position: absolute;
    opacity: 0;
}

.header-cart-empty-title {
    position: absolute;
    bottom: 20%;
    left: 10%;
}

.header-cart-wraper {
    padding: 0.5em
}

.header-cart-title {
    opacity: 0.5;
}

.header-cart-list-element {
    margin-top: 1em;
    max-height: 400px;
    overflow: scroll;
}

.header-cart-element-wrapper {
    display: flex;
    box-sizing: border-box;
    padding: 0.3em;
}

.header-cart-element-delete {
    align-self: center;
    font-weight: bold;
    margin-right: 1em;
}

.header-cart-element-delete:hover {
    color: var(--main-bg-color);
}

.header-cart-button-cart {
    float: right;
    border: none;
    color: white;
    margin-bottom: 1em;
    padding: 0.5em;
    background-color: var(--main-bg-color);
}

.header-cart-button-cart:hover {
    opacity: 0.7;
    outline: none;
}

.header-cart-button-cart:focus {
    outline: none;
}

.header-cart-divider {
    border-top: 1px dashed #9e9e9e;
    width: 90%;
    margin: auto;
    opacity: 0.3;
}

@keyframes opacity1 {
    from {
        opacity: 0;
        margin-top: 0.5em;
        display: none;
    }
    to {
        opacity: 1;
        margin-top: 0em;
        display: block;
    }
}

@-webkit-keyframes opacity1 {
    from {
        opacity: 0;
        margin-top: 0.5em;
        display: none;
    }
    to {
        opacity: 1;
        margin-top: 0em;
        display: block;
    }
}

@keyframes opacity {
    from {
        opacity: 0;
        margin-top: 0.5em;
    }
    to {
        opacity: 1;
        margin-top: 0em;
    }
}

@-webkit-keyframes opacity {
    from {
        opacity: 0;
        margin-top: 0.5em;
    }
    to {
        opacity: 1;
        margin-top: 0em;
    }
}

@media screen and (max-width: 1280px) {
    .header-wrapper {
        width: 100%;
    }
}

@media screen and (max-width: 768px) {
    .header-cart-container {
        margin-right: 1em;
    }

    .header-cart-container:hover .header-cart-list-cart {
        display: none;
    }

    .header-cart-text {
        display: none;
    }

    .fa-shopping-cart {
        font-size: 1em;
    }
}