.toolbar-container {
    /*background-color: var(--main-bg-color);*/
    width: 100%;
    box-sizing: border-box;
    z-index: 5;
    padding-left: 1em;
    padding-right: 1em;
}

.toolbar-link {
    color: white;
    /*font-weight: bold;*/
    padding: 1em;
    transition: color 0.5s ease-out;
    line-height: 2.2em;
    font-weight: bold;
}

.toolbar-link:hover {
    color: black;
}

.toolbar-navigation {
    position: absolute;
    z-index: 3;
    display: none;
}

.navigation-wrapper {
    position: relative;
    height: 100%;
    box-sizing: border-box;
    z-index: 3;
    display: flex;
}

.toolbar-title:hover .toolbar-navigation {
    display: block;
    opacity: 1;
    animation: showNavi 0.2s ease-out;
}

.toolbar-search {
    position: absolute;
    right: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 25%;
    min-width: 240px;
}

.toolbar-element-link {
    display: flex;
}

.toolbar-icon-menu {
    display: none;
}

.fa-bars {
    color: white !important;
}

@media screen and (max-width: 1280px) {
    .toolbar-element-link {
        display: none;
    }

    .toolbar-icon-menu {
        display: block;
        padding: 1em 1em 1em 0.5em;

    }

    .toolbar-title {
        display: none;
    }

    .toolbar-container {
        padding-left: 0.5em;
        padding-right: 0.5em;
    }
}

@keyframes showNavi {
    0% {
        opacity: 0;
        margin-top: 2em;
    }
    100% {
        opacity: 1;
        margin-top: 0;
    }
}