.searchbar-container {
    display: flex;
    position: relative;
    align-items: center;
    flex-grow: 1;
    position: relative;
    box-sizing: border-box;
}

.searchbar-input {
    padding: 0.8em;
    flex-grow: 1;
    border: 1px solid #e0e0e0;
    -webkit-border-radius: 0px;
    box-sizing: border-box;
    font-size: 1em;
    appearance: none;
    -webkit-appearance: none;
}

.searchbar-input:focus {
    outline: none;
}

.searchbar-icon {
    position: absolute;
    right: 1em;
    color: grey !important;
    cursor: pointer;
    padding: 0.5em;
    box-sizing: border-box;
}

.searchbar-delete-icon {
    position: absolute;
    right: 3em;
    color: #424242 !important;
    cursor: pointer;
    padding: 0.5em;
    box-sizing: border-box;
}

@media screen and (max-width: 1280px) {
    .searchbar-icon {
        right: 0.5em;
    }

    .searchbar-delete-icon {
        right: 3em;
    }

    .searchbar-input {
        padding: 0.7em;
        font-size: 0.8em;
    }
}
