.dialog-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.3);
    animation: dialog 0.2s ease-out;
    animation-fill-mode: forwards;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dialog-content {
    position: relative;
    background-color: white;
    overflow: scroll;
    /*height: 500px;*/
    animation: content 0.5s ease-out;
    border-radius: 5px;
    padding: 1em;
    box-sizing: border-box;
}

.dialog-close-button {
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    background-color: inherit;
    transition: opacity 0.3s ease-out;
    font-size: 1.2em;
    opacity: 0.6;
    color: black;
}

.dialog-close-button:hover {
    opacity: 1;
}

@keyframes dialog {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }

}

@keyframes content {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media screen and (max-width: 768px) {
    .dialog-content {
        width: 100%;
    }
}