.navigation-nav-container {
    margin: 0px;
    padding: 0px;
    width: 220px;
    position: relative;
    border: 1px solid rgba(187, 187, 187, 0.3);
    box-sizing: border-box;
    background-color: white;
}

.fa-angle-right {
    color: white !important;
    margin-left: 0.5em;
    margin-right: 0.5em;
    font-size: 1.2em;
}

.fa-angle-right {
    font-size: 1em;
    color: #bdbdbd !important;
    transition: 0.2s;
}

.navigation-divider {
    border-bottom: 1px solid black;
    opacity: 0.1;
    width: 90%;
}

.navigation-nav-container li {
    list-style: none;
}

.navigation-title-container {
    display: flex;
    width: 100%;
    color: black;
    background-color: white;
    align-items: center;
    padding-left: 1em;
    padding-top: 0.7em;
    padding-bottom: 0.7em;
    cursor: pointer;
    position: relative;
    box-sizing: border-box;
    transition: color 0.2s linear;
}

.navigation-nav {
    position: relative;
}

.navigation-nav:hover .navigation-title-container {
    background-color: var(--main-bg-color);
    color: white;
}

.navigation-title {
    flex-grow: 1;
    font-size: 0.9em;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently*/
}

.navigation-sub-title {
    flex-grow: 1;
    font-size: 0.9em;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently*/
    color: black;
    transition: color 0.3s ease-out;
}

.navigation-nav .navigation-nav-list {
    display: none;
    overflow: auto;
    background-color: white;
    height: auto;
    border-left: 2px solid var(--main-bg-color);
}

.navigation-nav-list-1 {
    display: flex;
    width: 100%;
    background-color: white;
    align-items: center;
    padding-left: 1em;
    padding-top: 0.8em;
    padding-bottom: 0.8em;
    cursor: pointer;
    position: relative;
    box-sizing: border-box;
}
.navigation-nav-list-1{
    color: black;
    font-size: 0.9em;
    transition: color 0.3s ease-out;
}
.navigation-nav-list-1:hover{
    background-color: var(--main-bg-color);
}
.navigation-nav-list-1:hover{
    color: white;
}

@media screen and (min-width: 901px) {
    .navigation-nav .navigation-nav-list {
        position: absolute;
        width: 220px;
        left: 100%;
        color: black;
        top: 0;
        -webkit-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
        -moz-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
        box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.1);
    }

    .navigation-nav:hover .navigation-nav-list {
        display: block;
        animation: childList 0.3s;
        -webkit-animation: childList 0.3s;
        display: grid;
        grid-template-columns: 1fr;
        z-index: 100;
    }
}
@keyframes childList {
  from {
    opacity: 0;
    margin-left: 2em;
  }
  to {
    opacity: 1;
    margin-left: 0em;
}
}

@media screen and (max-width: 768px) {
    .navigation-small-screen-list .navigation-nav-list {
        grid-template-columns: 1fr;
        display: block;
    }

    .navigation-small-screen-list:hover .navigation-nav-list {
        grid-template-columns: 1fr;
        display: block;
    }
}
