.SkeletonTemplate-animation {
  opacity: 1;
  top: 0;
  animation-name: skeletonwaving;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  -webkit-animation-name: skeletonwaving;
  -webkit-animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: infinite;
}
@keyframes skeletonwaving {
  0% {
    left: -500%;
  }
  1% {
    left: -100%;
  }
  90% {
    left: 200%;
  }
  100% {
    left: 500%;
  }
}
@-webkit-keyframes skeletonwaving {
  0% {
    left: -40%;
  }
  20% {
    left: -40%;
  }
  80% {
    left: 140%;
  }
  100% {
    left: 140%;
  }
}