.mobile-menu-container {
    position: fixed;
    width: 0vw;
    height: 100vh;
    top: 0;
    left: 0;
    opacity: 0;
    -webkit-transition: opacity 0.2s ease-out;
    transition: opacity 0.2s ease-out;
    background-color: rgba(0, 0, 0, 0.1);
    z-index: 5;
}

.mobile-menu-container.open-container {
    width: 100vw;
    opacity: 1;
}

.mobile-menu-content {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 5;
    background-color: white;
    width: 0px;
    -webkit-transition: width 0.3s;
    transition: width 0.3s;
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch; /* Lets it scroll lazy */
}

.mobile-menu-content.open {
    width: 270px;
}

.mobile-menu-back-icon {
    padding: 0.5em;
    border-radius: 60%;
    position: absolute;
    right: 0;
    margin-top: 0.3em;
    margin-left: 0.3em;
    color: black;
}

.mobile-menu-back-icon:hover {
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.1);
}

.mobile-menu-toolbar-container {
    margin-top: 1em;
    /*overflow-y: scroll;*/
}
.mobile-menu-logo {
    width: 150px;
    margin-left: 3.5em;
    margin-top: 3em;
}
