.main-container {
    width: 100%;
    padding-bottom: 5em;
    min-height: 100vh;
    flex-grow: 1;
}

.main-icon-wrapper {
    width: 100%;
    transition: 0.3s ease-in-out;
    height: 100%;
    position: relative;
    margin-left: 1em;
}

.main-icon-desktop {
    position: fixed;
    bottom: 0.8em;
    left: 1em;
    z-index: 1000;
}

.main-contact-element {
    display: flex;
    margin-bottom: 0.25em;
}

.main-icon-other {
    width: 2em;
    height: 2em;
    align-self: center;
    border-radius: 50%;
}

.main-icon-title {
    font-size: 0.8em;
    padding: 0.8em;
    background-color: #d9910e;
    color: white;
    border-radius: 5px;
    margin-left: 1.5em;
    position: relative;
}

.main-icon-title:before {
    content: '';
    position: absolute;
    width: 1em;
    height: 1em;
    left: 0;
    transform: translateX(-100%);
    box-sizing: border-box;
    border-top: 0.5em solid transparent;
    border-left: 0.5em solid transparent;
    border-bottom: 0.5em solid transparent;
    border-right: 0.5em solid #d9910e;
}

.main-phone-icon {
    border-radius: 50%;
    width: 4em;
}

.element-1 {
    opacity: 0;
    animation: element-opacity 0.2s ease-in;
    animation-delay: 0.2s;
    animation-fill-mode: forwards;
}

.element-2 {
    opacity: 0;
    animation: element-opacity 0.2s ease-in;
    animation-delay: 0.1s;
    animation-fill-mode: forwards;

}

.element-3 {
    opacity: 0;
    animation: element-opacity 0.2s ease-in;
    animation-fill-mode: forwards;

}
.main-contact {
    align-self: flex-start;
}
.main-contact:hover {
    cursor: pointer;
}

@media screen and (max-width: 1280px) {
    .AppTemplate-main-wrapper {
        width: 100%;
    }
}

@media screen and (max-width: 768px) {

}

@keyframes element-opacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

