.SlideTemplate-container {
    width: 100%;
    position: relative;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently*/
    box-sizing: border-box;
    overflow-x: scroll;
    overflow-y: hidden;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}

.SlideTemplate-container::-webkit-scrollbar {
    display: none;
}

.SlideTemplate-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    /*transition: all 0.25s ease 0s;*/
}

.SlideTemplate-container:active {
    cursor: grab;
    z-index: 9999;
}

/* Hide scrollbar for Chrome, Safari and Opera */
/*.SlideTemplate-wrapper::-webkit-scrollbar {*/
/*  display: none;*/
/*}*/
/* Hide scrollbar for IE and Edge */
.SlideTemplate-wrapper {
    -ms-overflow-style: none;
}

/* arrows */
.SlideTemplate-arrow-container {
    position: absolute;
    top: 50%;
    height: 1.5em;
    width: 1.5em;
    transform: translateY(-50%);
    background-color: white;
    cursor: pointer;
    /*border-radius: 50%;*/
    z-index: 2;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
    font-size: 0.7em;
    opacity: 0.7;
    padding: 0.3em;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: width 0.1s ease-out, height 0.1s ease-out;

}

.SlideTemplate-container:hover .SlideTemplate-arrow-container {
    width: 2em;
    height: 2em;
    font-size: 0.9em;
}

.slide-left-arrow {
    left:2px;
}

.slide-right-arrow {
    right: 2px;
}

.left-arrow .fa-sort-up {
    transform: rotate(-90deg);
    color: #424242 !important;
    font-size: 1.5em;
}

.right-arrow .fa-sort-up {
    transform: rotate(90deg);
    color: #424242 !important;
    font-size: 1.5em;
}

.SlideTemplate-item-container {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.SlideTemplate-item {
    color: white;
    font-size: 2em;
}
