.header-locations-container {
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
}

.header-locations-location-wrapper {
    display: flex;
    flex-direction: row;
    margin-right: 4em;
    align-items: center;
    justify-content: flex-start;
}

.header-icon-container {
    border-radius: 1em;
    /*background-color: var(--second-bg-color);*/
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 0.5em;
    margin-right: 0.5em;
    color: white;
}

.fa-phone-volume {
    color: var(--second-bg-color) !important;
    font-size: 2em;
    transform: rotate(-45deg);
}

.header-locations-time-number {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
}

.header-locations-number {
    font-size: 0.9em;
}

.header-location-hotline {
    color: var(--main-bg-color);
    font-weight: bold;
    margin-top: 0.2em;
    font-size: 1em;
}

.fa-map-marker-alt {
    color: var(--second-bg-color);
    font-size: 2em;
}

@media screen and (max-width: 1280px) {

}

@media screen and (max-width: 768px) {
    .header-locations-container {
        display: none;
    }

    .searchbar-menu {
        display: block;
        font-size: 1.2em;
        cursor: pointer;
        padding: 0.5em;
    }

    .fa-bars {
        color: grey !important;
    }
}