.body-template-container {
    width: 100%;
}
.body-template-wrapper {
    width: 80%;
    max-width: 1280px;
    margin: auto;
    position: relative;
    padding: 0.5em 1em 0.5em 1em;
    box-sizing: border-box;
}
@media screen and (max-width: 1280px){
    .body-template-wrapper {
        width: 100%;
        padding: 1em;
    }
}
@media screen and (max-width: 768px){
    .body-template-wrapper {
        width: 100%;
        padding: 0.5em;
    }
}