.Start-detail-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-content: center;
  width: 100%;
}
.Start-common-container {
  padding: 1em;
}
.Start-common-title {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size: 1em;
  font-weight: bold;
}
.Start-common-item {
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size: 0.8em;
}
.Start-common-item:hover {
  color: #2196f3;
}
.Start-support-container {
  padding: 1em;
}
.Start-support-title {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size: 1em;
  font-weight: bold;
}
.Start-support-item {
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size: 0.8em;
}
.Start-support-item:hover {
  color: #2196f3;
}
.Start-locations-container {
  padding: 1em;
}
.Start-locations-title {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size: 1em;
  font-weight: bold;
}
.Start-locations-items {
  padding: 0;
  margin: 0;
}
.Start-locations-item {
  cursor: pointer;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size: 0.8em;
}
.Start-locations-item-name {
  font-weight: bold;
}
.Start-locations-item-phone {
}
.Start-fanpage-container {
  padding: 1em;
}
@media screen and (max-width: 1280px) {
  .Start-detail-container {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media screen and (max-width: 768px) {
  .Start-locations-container {
    grid-column: 1/3;
  }
  .Start-fanpage-container {
    display: none;
  }
}
