.detail-head-container {
    display: flex;
    flex-direction: column;
    width: 60%;
    box-sizing: border-box;
}

.detail-head-title {
    font-size: 28px;
    color: #363636;
    line-height: normal;
    font-family: "Roboto", sans-serif;
    margin: 0px 0 15px;
    font-weight: 600;
}

.detail-head-remain {
    font-size: 0.9em;
}

.detail-head-remain-text {
    font-weight: bold;
    color: var(--main-bg-color);
}

.detail-head-price {
    display: block;
    overflow: hidden;
    margin-top: 1em;
    background-color: #f5f5f5;
    width: 100%;
    padding: 1em;
    box-sizing: border-box;
}

.detail-head-saleContainer {
    display: flex;
    flex-direction: row;
}

.detail-head-salePrice {
    font-size: 15px;
    text-decoration: line-through;
    margin-right: 0.5rem
}

.detail-head-salePercent {
    font-size: 12px;
    font-weight: bold;
    color: white;
    background-color: var(--main-bg-color);
    padding: 5px;
    border-radius: 3px;
}

.detail-head-mainPrice {
    font-size: 1.875rem;
    font-weight: 500;
    color: var(--main-bg-color);
}

.detail-head-divider {
    border-top: 0.5px solid #bbb;
    width: 100%;
    opacity: 0.4;
}

.detail-head-amount {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
}

.detail-head-amountTitle {
    line-height: 2rem;
    margin-right: 3rem;
}

.detail-head-button {
    border: 1px solid #bbb;
    width: 2rem;
    height: 2rem;
}

button:focus {
    outline: none;
}

button:hover {
    cursor: pointer;
}

.detail-head-btnCard {
    padding: 1rem 0 1rem 0;
    width: 10rem;
    font-size: 14px;
}

.detail-head-cardContainer {
    margin-top: 1.5rem;
}

.add {
    background-color: rgba(5, 105, 32, 0.1);
    border: 1px solid var(--main-bg-color);
    color: var(--main-bg-color);
}

.add:hover {
    background-color: rgba(5, 105, 32, 0.2);
}

.now {
    background-color: var(--main-bg-color);
    border: 1px solid var(--main-bg-color);
    color: white;
    margin-left: 1rem;
}

.now:hover {
    opacity: 0.8;
}

.detail-head-input {
    border: none;
    width: 3rem;
    border-top: 1px solid #bbb;
    border-bottom: 1px solid #bbb;
    text-align: center;
    outline: none;
    border-radius: unset;
    appearance: none;
    -webkit-appearance: none;
}

.detail-head-phone {
    margin-top: 1rem;
    font-size: 14px;
}

.detail-head-phoneNumber {
    font-weight: bold;
    color: var(--main-bg-color);
}

.detail-head-link-product, detail-head-link-product:active {
    color: black;
}

.detail-head-link-product:hover {
    color: var(--main-bg-color);
}

@media screen and (min-width: 1151px) {
    .specific {
        margin-top: 0rem;
        margin-left: 2rem;
    }
}

@media screen and (max-width: 1280px) {
    .specific {
        margin-top: 0rem;
        margin-left: 2rem;
    }
}

@media screen and (max-width: 768px) {
    .specific {
        margin-top: 1rem;
        margin-left: 0rem;
        width: 100%;
    }
}
