.Middle-container {
  width: 100%;
  margin-top: 1em;
  margin-bottom: 1em;
  display: grid;
  grid-template-columns: 1fr 1fr auto;
}
.Middle-headquarter-container {
  padding: 1em;
}
.Middle-headaurter-title {
  font-weight: bold;
}
.Middle-headquarter-side {
  font-size: 0.8em;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
.Middle-headquarter-side-phone {
  font-weight: bold;
  color: #2196f3;
  cursor: pointer;
}
.Middle-connect-us-container {
  padding: 1em;
}
.Middle-connect-us-icon-wrapper {
  display: flex;
  align-items: flex-start;
}
.Middle-connect-us-icon-title {
  font-weight: bold;
  min-width: 50%;
}
.Middle-icon-container {
  border-radius: 1px;
  margin-left: 0.3em;
  margin-right: 0.3em;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.Middle-switchboard-container {
  font-size: 0.8em;
}
.Middle-switchboard {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
.Middle-switchboard-phone {
  color: #2196f3;
  cursor: pointer;
  font-weight: bold;
}
.Middle-approve-container {
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Middle-approve-item {
  height: 45px;
  margin-left: 0.5em;
  cursor: pointer;
}
.fa-facebook-square {
  font-size: 1.5em;
  color: #4464b4 !important;
}
.fa-youtube-square {
  font-size: 1.5em;
  color: #ff0000 !important;
}
.fa-shopify {
  font-size: 1.5em;
  color: #fa5230 !important;
}
@media screen and (max-width: 768px) {
  .Middle-container {
    grid-template-columns: 1fr;
  }
  .Middle-headquarter-container {
  }
}
